
#compSelect{
  background-image: linear-gradient(180deg, #3C134C, #DC040D);
  height: 10%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#compList{
  width: 350px;
  height: 50px;
  border-style: solid;
  border-width: 4px;
  border-color: black;
  border-radius: 10px;
  color: #3C134C;
  text-shadow: -1px 0 rgb(255, 255, 255), 0 1px rgb(255, 255, 255), 1px 0 rgb(255, 255, 255), 0 -1px rgb(255, 255, 255);
  font-size: large;
  font-family: "Arial Black", Gadget, sans-serif;
  background-color: #b2b7bb;
}

#container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  background-image: url(football-stadium.jpg);
}


#standings{
  width: 50%;
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Pos{
  color: #FFF;
  background-color: #3C134C;
}
.TeamName{
  color: #FFF;
  background-color: #DC040D;
}
.GP{
  color: #FFF;
  background-color: #3C134C;
}
.Won{
  color: #FFF;
  background-color: #DC040D;
}
.Loss{
  color: #FFF;
  background-color: #3C134C;
}
.GD{
  color: #FFF;
  background-color: #DC040D;
}
.Points{
  color: #FFF;
  background-color: #3C134C;
}
#StandHeaders th{
  background-color: #19081f;
}
#TableStand{
  box-sizing: border-box;
}

/* *** RESULT CSS *** */
#results{
  width: 50%;
  height: 100vh;
  padding-top: 10vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
}
#MatchDay{
  width: 100px;
  border-style: solid;
  border-width: 4px;
  border-color: black;
  border-radius: 10px;
  color: #3C134C;
  text-shadow: -1px 0 rgb(255, 255, 255), 0 1px rgb(255, 255, 255), 1px 0 rgb(255, 255, 255), 0 -1px rgb(255, 255, 255);
  font-size: small;
  font-family: "Arial Black", Gadget, sans-serif;
  background-color: #b2b7bb;
}
#table{
  display:flex;
  flex-direction:column;
  border-collapse: collapse;
}
td, tr{
  border-collapse: collapse;
}
#match{
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  justify-content: center;
}
#ScoreInfo{
  width: 100%;
}
.Team{
  width: 30%;
  background-image: linear-gradient(#DC040D, #3C134C);
  color:white;
}
.Score{
  width: 10%;
  background-image: linear-gradient(#DC040D, #3C134C);
}
.HomeOrAway{
  width: 5%;
  background-color:#3C134C;
  color: white;
}
#time{
  background-image: linear-gradient(#3C134C, #DC040D);
  color: #FFF;
}
.Score{
  color: #FFF;
}